body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  border: 0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: rgba(0, 0, 0, 1);
  background: -webkit-gradient(
    left top,
    right top,
    color-stop(0%, rgba(0, 0, 0, 1)),
    color-stop(23%, rgba(11, 58, 13, 1)),
    color-stop(33%, rgba(11, 58, 13, 1)),
    color-stop(70%, rgba(31, 42, 0, 1)),
    color-stop(86%, rgba(28, 28, 28, 1)),
    color-stop(100%, rgba(19, 19, 19, 1))
  );
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 1) 0%,
    rgba(11, 58, 13, 1) 23%,
    rgba(11, 58, 13, 1) 33%,
    rgba(31, 42, 0, 1) 70%,
    rgba(28, 28, 28, 1) 86%,
    rgba(19, 19, 19, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#131313', GradientType=1 );

  font-size: 24px;
  font-family: "Roboto";
}

.app {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 80px;
}

button {
  padding: 10px;
  border-radius: 3px;
  text-transform: uppercase;
  font-weight: bold;
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.3);
  margin-top: 20px;
  margin-bottom: 20px;
  cursor: pointer;
  background-color: #de9200;
  transition: background 0.1s linear;

  position: relative;
  max-width: 200px;
  left: calc(50% - 100px);
}

button:hover {
  background-color: #ffa800;
}

button:hover,
button:focus {
  outline: none;
}

img {
  width: 100%;
  max-width: 260px;

  background-color: rgba(255, 255, 255, 0.8);
  padding: 10px;
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.3);
  border-radius: 10px;

  display: block;
  position: relative;
  left: calc(50% - 130px);
}

p {
  font-size: 48px;
  color: #ccc;
  text-align: center;
  -webkit-animation: fade 0.2s;
          animation: fade 0.2s;
}

span {
  font-size: 0.75em;
  color: #bbb;
  color: #daa646;
  text-transform: capitalize;
}

img {
  margin-top: 5px;
}

@media (min-width: 680px) {
  .app {
    padding-left: 160px;
    padding-right: 160px;
  }
}

@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #fff;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #fff transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}
@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.faq-footer {
  width: 100%;
  position: fixed;
  bottom: -5px;
  left: 0%;
  padding: 3px;
  margin: 0;
}
.faq-footer--text {
  margin: 2px auto;
  text-align: center;
  font-size: 25px;
}
.faq-footer--text a {
  text-decoration: none;
  color: gray;
}
.corazon {
  background-color: red;
  display: inline-block;
  height: 10px;
  margin: 0 10px;
  top: 0;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  width: 10px;
}

.corazon:before,
.corazon:after {
  content: "";
  background-color: red;
  border-radius: 50%;
  height: 10px;
  position: absolute;
  width: 10px;
}

.corazon:before {
  top: -5px;
  left: 0;
}

.corazon:after {
  left: 5px;
  top: 0;
}

@-webkit-keyframes latido {
  0% {
    -webkit-transform: scale(1) rotate(-45deg);
            transform: scale(1) rotate(-45deg);
  }
  20% {
    -webkit-transform: scale(1.25) rotate(-45deg);
            transform: scale(1.25) rotate(-45deg);
  }
  40% {
    -webkit-transform: scale(1.5) rotate(-45deg);
            transform: scale(1.5) rotate(-45deg);
  }
}

@keyframes latido {
  0% {
    -webkit-transform: scale(1) rotate(-45deg);
            transform: scale(1) rotate(-45deg);
  }
  20% {
    -webkit-transform: scale(1.25) rotate(-45deg);
            transform: scale(1.25) rotate(-45deg);
  }
  40% {
    -webkit-transform: scale(1.5) rotate(-45deg);
            transform: scale(1.5) rotate(-45deg);
  }
}

.corazon {
  -webkit-animation: latido 1s infinite;
          animation: latido 1s infinite; /* nuestro corazón tiene latidos infinitos */
}

